
import Events from './Events';
import { EVENTS, ACTIONS } from '../config';

export default class MessageBus {
    constructor(target) {
        this.target = target;
        this.events = new Events();
        this.listenForMessages();
    }

    listenForMessages() {
        window.addEventListener('message', this.onMessageReceived.bind(this), false);
    }

    sendMessage(obj) {
        // console.log('SEND MESSAGE TO CHILD', obj, JSON.stringify(obj));
        const message = JSON.stringify(obj);
        this.target.postMessage(message, '*');
    }

    onMessageReceived(evt) {
        const messageData = JSON.parse(evt.data);
        const action = messageData.action;
        // console.log('PARENT got a message:', messageData);

        if (action) {
            switch (action) {
                case ACTIONS.height:
                    this.events.emitEvent(EVENTS.height, {
                        height: messageData.height
                    });
                    break;
                case ACTIONS.route:
                    this.events.emitEvent(EVENTS.route, {
                        route: messageData.route,
                    });
                    break;

                default:
                    break;
            }
        }
    }
}