import './assets/scss/index.scss';

import { EVENTS, PATH } from './assets/js/config';
import Events from './assets/js/utils/Events';
import MessageBus from './assets/js/utils/MessageBus';

class Chilidex {
    constructor() {
        this.createIframe();
        this.events = new Events();
        this.addListeners();
    }

    createIframe() {
        const
            _iframeSource = `${ PATH + window.location.pathname }`,
            _iframeId = 'childSpa';

        this.iframeId = _iframeId;

        // Create the iframe
        const
            iframe = document.createElement('iframe'),
            mainEl = document.querySelector('.main');

        iframe.setAttribute('src', _iframeSource);
        iframe.setAttribute('id', _iframeId);
        iframe.style.width = '100%';
        iframe.style.height = '0';

        mainEl.appendChild(iframe);

        iframe.onload = this.createMessageBus.bind(this);
    }

    createMessageBus() {
        const
            iframe = document.getElementById('childSpa'),
            bus = new MessageBus(iframe.contentWindow);

        this.iframe = iframe;
        this.bus = bus;

        let _url = (window.location.port && window.location.port !== '')
            ? `${window.location.hostname}:${window.location.port}`
            : window.location.hostname;

        bus.sendMessage({
            'action': 'start',
            'parentURL' : `${_url}`
        });
    }

    addListeners(){
        this.events.addListener(EVENTS.height, this.onHeightUpdated.bind(this));
        this.events.addListener(EVENTS.route, this.onRouteChanged.bind(this));
    }

    onHeightUpdated(evt) {
        this.iframe.style.height = '0';
        this.iframe.style.height = evt.height + 'px';
    }

    onRouteChanged(evt) {
        // console.log('PARENT __ onRouteChanged', evt);
        window.history.replaceState(evt, 'Chilidex (Parent)', evt.route);
    }
}

new Chilidex();